import React, { useState, useEffect} from 'react';
import axios from 'axios';
import { PayPalButton } from 'react-paypal-button-v2';
import { Row, Col, ListGroup, Image, Card, Button} from 'react-bootstrap';
import {useDispatch, useSelector} from 'react-redux';
import Message from '../components/Message.js';
import Loader from '../components/Loader';
import {getOrderDetails, payOrder, deliverOrder} from '../actions/orderActions.js';

import {Link} from 'react-router-dom';

import {ORDER_PAY_RESET, ORDER_DELIVER_RESET} from '../constants/orderConstants'


const OrderScreen = ({ match, history }) => {

    const orderId = match.params.id
    const [sdkReady, setSdkReady] = useState(false)

  const dispatch = useDispatch();



  const userLogin = useSelector((state) => state.userLogin)
    const { userInfo } = userLogin

  const orderDetails = useSelector(state => state.orderDetails);
  const { order, loading, error} = orderDetails

  const orderPay = useSelector(state => state.orderPay);
  const { success:successPay, loading:loadingPay} = orderPay

  const orderDeliver = useSelector(state => state.orderDeliver);
  const { success:successDeliver, loading:loadingDeliver} = orderDeliver


  useEffect(() => {

    if(!userInfo){
      history.push('/login')
    }

    const addPayPalScript = async() => {

        const { data: clientId } = await axios.get('/api/config/paypal')
        const script  = document.createElement('script')
        script.type = 'text/javascript'
        script.src = `https://www.paypal.com/sdk/js?client-id=${clientId}`
        script.async = true
        script.onload = () => {
            setSdkReady(true)
        }
        
        document.body.appendChild(script)
    }

    if(!order || successPay || successDeliver){
        dispatch({type: ORDER_PAY_RESET})
        dispatch({type: ORDER_DELIVER_RESET})
        dispatch(getOrderDetails(orderId))
    } else if(!order.isPaid){
        addPayPalScript()
    } else {
        setSdkReady(true)
    }

 

  
  },[dispatch, order, orderId, successPay, successDeliver, history, userInfo] );

 const successPaymentHandler = (paymentResult) => {
     console.log(paymentResult)
     dispatch(payOrder(orderId, paymentResult))

 }

 const deliverHandler =  () => {
   dispatch(deliverOrder(order))
 }

  return loading ? <Loader /> : error ? <Message variant='danger'>{error}</Message> :
    <>
    <h1>Order {order._id}</h1>
     <Row>
        <Col md={8}>
          <ListGroup variant="flush">
            <ListGroup.Item>
              <h2>Shipping</h2>
              <p>
              <strong>Name :</strong> {order.user.name}
              </p>
              <p>
                  <a href={`mailto:${order.user.email}`}>{order.user.email}</a>
              </p>
              
              <p>
                <strong>Address : </strong>
                {order.shippingAddress.address}, {order.shippingAddress.city},
                {order.shippingAddress.postalCode},{order.shippingAddress.country}
              </p>

              {order.isDelivered ? (<Message variant='success'>Delivered on {order.deliveredAt}</Message>) : (<Message variant='danger'>Not Delivered yet</Message>)}
            </ListGroup.Item>

            <ListGroup.Item>
              <h2>Payment</h2>
              <p>
                <strong>Payment method : </strong>
                {order.paymentMethod}
              </p>
              {order.isPaid ? (<Message variant='success'>Paid on {order.paidAt}</Message>) : (<Message variant='danger'>Not Paid</Message>)}
            </ListGroup.Item>

            <ListGroup.Item>
              <h2> Order Items</h2>
              {order.orderItems.length === 0 ? (
                <Message>No orders dispatched</Message>
              ) : (
                <ListGroup variant="flush">
                  {order.orderItems.map((item, index) => (
                    <ListGroup.Item key={index} class="mx-auto">
                      <Row>
                        <Col md={3}>
                          <Image
                            src={item.image}
                            alt={item.name}
                            fluid
                            rounded
                          />
                        </Col>
                        <Col>
                          <Link to={`/product/${item.product}`}>
                            {item.name}
                          </Link>
                        </Col>
                        <Col md={4}>
                          {item.qty} x ₹{item.price} = ₹{item.qty * item.price}
                        </Col>
                      </Row>
                    </ListGroup.Item>
                  ))}
                </ListGroup>
              )}
            </ListGroup.Item>
          </ListGroup>
        </Col>

        <Col md={4}>
          <Card>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h2>Order Summary</h2>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col>Items</Col>
                  <Col>₹{order.itemsPrice}</Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col>Shipping</Col>
                  <Col>₹{order.shippingPrice}</Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col> SGST Tax</Col>
                  <Col>₹{order.taxsPrice}</Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col> CGST Tax</Col>
                  <Col>₹{order.taxcPrice}</Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col>Total</Col>
                  <Col>₹{order.totalPrice}</Col>
                </Row>
              </ListGroup.Item>
             {!order.isPaid && (
                 <ListGroup.Item>
                     {loadingPay && <Loader />}
                     {!sdkReady ? <Loader /> : (
                         <PayPalButton amount={order.totalPrice} onSuccess={successPaymentHandler}/>
                     )}
                 </ListGroup.Item>
             )}
             {loadingDeliver && <Loader />}           
             {userInfo && userInfo.isAdmin && !order.isDelivered && (
               <ListGroup.Item>
               <Button type='button' className='btn btn-block' onClick={deliverHandler}>
                 Mark as Delivered
               </Button>
               </ListGroup.Item>
             )}
              
            </ListGroup>
          </Card>
        </Col>
      </Row>
    
    </>
}


export default OrderScreen
